// Import a couple modules for testing.
import { sayHelloTo } from './mod1'
import addArray from './mod2'

// Import a logger for easier debugging.
import ulog from 'ulog'
const log = ulog('app:log')

// The logger should only be enabled if we’re not in production.
if (ENV !== 'production') {
  // Enable the logger.
  ulog.enable('*')
  log.log('Logging is enabled!')

  // Enable LiveReload
//  document.write(
//    '<script src="http://' + (location.host || 'localhost').split(':')[0] +
//    ':35729/livereload.js?snipver=1"></' + 'script>'
//  );
} else {
  ulog.disable()
}

// Run some functions from our imported modules.
const result1 = sayHelloTo('Jason')
const result2 = addArray([1, 2, 3, 4])

// Print the results on the page.
log.info(`sayHelloTo('Jason') => ${result1}\n\n`)
log.info(`addArray([1, 2, 3, 4]) => ${result2}`)
